const Media = React.lazy(() => import('./Media/Media'));

class SelectAssets extends Component {
    constructor() {
        super();
        this.state = { showModal:false };
    }
    render() {
        const imageStyle = {
            paddingBottom: '150%', width: '100%'
        };
        const {columnClasses,responseData} = this.props
        return  <div className="card">
                    <div className="card-body pe-2">
                        <div className="row gx-1 w-100 select_assets pe-1">
                            {
                                responseData.assets.map((peItem) => <div className={columnClasses}>
                                <div className="mb-1">
                                    <div className="card-body d-flex flex-column p-1">
                                        <div className="d-flex align-items-center cursor-pointer">
                                            <div className="position-relative rounded-1 bg-light overflow-hidden w-100" style={imageStyle}> 
                                            <Media mediaType={peItem.type} postFile={peItem.postFile} mediaHeight={400} mediaWidth={400} socialNetwork={peItem.networkId} textContent={peItem.content} rating={peItem.rating} />
                                                {/* <img className="img-fluid position-absolute w-100" loading="lazy" src={'https://images.unsplash.com/photo-1670373314792-4bdf07cba6be?crop=entropy&cs=tinysrgb&fit=max&fm=jpg&ixid=MnwyMjUyOTR8MHwxfGFsbHw1fHx8fHx8Mnx8MTY3MDQxMjc1NA&ixlib=rb-4.0.3&q=80&w=400'} height={500} width={300} alt={'suit'} /> */}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>)
                            }
                        </div>
                    </div>
                </div>
    }
}

export default SelectAssets;